.list-page {
    flex: 1;
    display: flex;
    flex-direction: column;
    .tab {
        width: 100%;
        height: @size;
        padding: @space 0;
        display: flex;
        li {
            flex: 1;
            border-right: 1px solid @border-gray;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: 500ms;
            cursor: pointer;
            &:last-child {
                border-right: none;
            }
            &:hover,
            &.active {
                color: #fff;
                background-color: #6cf;
            }
        }
        border-bottom: 1px solid @border-gray;
    }
    .list-pnl {
        height: 100%;
        overflow: auto;
        >li {
            padding: 0 @space;
            border-bottom: 1px solid @border-gray;
            animation: slide 500ms ease-out;
            @keyframes slide {
                from {
                    opacity: 0;
                    transform: translateY(50%);
                }
                to {
                    opacity: 1;
                    transform: translateY(0);
                }
            }
            header {
                height: @size;
                display: flex;
                align-items: center;
                a {
                    color: #333;
                }
                .left {
                    width: @size / 2;
                    height: @size / 2;
                    img {
                        width: 100%;
                        height: 100%;
                        border-radius: 50%;
                    }
                }
                .center {
                    margin: 0 @space;
                    p {
                        height: 50%;
                        time {
                            color: @font-gray;
                        }
                    }
                }
                .right {
                    flex: 1;
                    position: relative;
                    .tag {
                        padding: (@space / 2) @space;
                        border-radius: @space / 3;
                        color: #fff;
                        background-color: #6cf;
                    }
                    .type {
                        right: @space;
                        padding: (@space / 2) @space;
                        border: 1px solid red;
                        color: red;
                        background-color: #fff;
                        transform: rotate(30deg);
                        position: absolute;
                        &.green {
                            color: limegreen;
                            border: 1px solid limegreen;
                        }
                    }
                }
            }
            h2 {
                font-size: @big;
            }
            footer {
                height: @size;
                padding: @space 0;
                margin-top: @space;
                border-top: 1px solid @border-gray;
                display: flex;
                >div {
                    flex: 1;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border-right: 1px solid @border-gray;
                    &:last-child {
                        border-right: none;
                    }
                    i {
                        font-size: 1em;
                        margin: 0 @space / 2;
                    }
                }
            }
        }
    }
}


