@size: 1rem;
@space: @size / 5;
@font-gray: #777;
@border-gray: #ddd;

@small: @normal * 0.8;
@normal: .25rem;
@big: @normal * 1.2;
@x-big: @big * 1.2;
@xx-big: @x-big * 1.2;

@import "reset";
@import "list";

@font-face {
  font-family: 'iconfont';  /* project id 228481 */
  src: url('//at.alicdn.com/t/font_4mv0nur9o4j9k9.eot');
  src: url('//at.alicdn.com/t/font_4mv0nur9o4j9k9.eot?#iefix') format('embedded-opentype'),
  url('//at.alicdn.com/t/font_4mv0nur9o4j9k9.woff') format('woff'),
  url('//at.alicdn.com/t/font_4mv0nur9o4j9k9.ttf') format('truetype'),
  url('//at.alicdn.com/t/font_4mv0nur9o4j9k9.svg#iconfont') format('svg');
}

.iconfont{
    font-family: "iconfont" !important;
    font-size: 16px;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
}

html,
body {
    height: 100%;
}
body {
    font-size: .2rem;
    background-color: #ddd;
    font-family: "MicroSoft YaHei";
    overflow: hidden;
}
#app {
    max-width: 7.5rem;
    height: 100%;
    margin: 0 auto;
    color: #333;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    >div {
        flex: 1;
    }
}
.menu {
    height: @size;
    background-color: #f6f6f6;
    display: flex;
    li {
        border-top: 1px solid @border-gray;
        flex: 1;
        display: flex;
        a {
            color: inherit;
            flex: 1;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            &.router-link-active {
                color: #6cf;
            }
        }
        i {
            height: 50%;
            font-size: 2em;
            display: inline-block;
        }
    }
}


